import React, { useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import {
 Content,
 Header,
 Page,
 pageTheme,
 ContentHeader,
} from '@backstage/core';
import { makeStyles, createStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import { Alert } from '@material-ui/lab';
import { DefaultApi } from '../../api/apis';
import Autocomplete from '@material-ui/lab/Autocomplete';
 
const useStyles = makeStyles((theme) =>
 createStyles({
   root: {
     display: 'flex',
     flexWrap: 'wrap',
     justifyContent: 'center',
   },
   margin: {
     margin: theme.spacing(3),
   },
   withoutLabel: {
     marginTop: theme.spacing(3),
   },
   textField: {
     width: '25ch',
   },
 }),
);
 
const initialUserState = {
 name: '',
 age: 69,
};
 
export default function Create() {
 const classes = useStyles();
 const profile = { givenName: 'ระบบจองห้องผ่าตัด' };
 const api = new DefaultApi();
 
 const [user, setUser] = useState(initialUserState);
 const [status, setStatus] = useState(false);
 const [alert, setAlert] = useState(true);
 
 const handleInputChange = (event) => {
   const { id, value } = event.target;
   setUser({ ...user, [id]: value });
 };
 
 const CreateUser = async () => {
   const res = await api.createUser({ user });
   setStatus(true);
   if (res.id != ''){
     setAlert(true);
   } else {
     setAlert(false);
   }
   const timer = setTimeout(() => {
     setStatus(false);
   }, 1000);
 };
 const patient = [
  { patient_id: '5001' },
  { patient_id: '5002'},]
  const room = [
    { room: '6001' },
    { room: '6002'},]
    const doctor = [
      { doctor_id: '4001' },
      { doctor_id: '4002'},]
 
 return (
   React.createElement(Page, { theme: pageTheme.home,}
     , React.createElement(Header, {
       title: `ใน${profile.givenName || ''} แพทย์สามารถจองห้องผ่าตัดที่นี่`,
       subtitle: "ผ่าแล้วเย็บคืนด้วย",}
     )
     , React.createElement(Content, null
       , React.createElement(ContentHeader, { title: "กรอกรายละเอียดการจอง",}
         , React.createElement('font', { color: "blue",}, "นายแพทย์ ทดสอบ ทดสอบ"  )
         , React.createElement(Button, {
               style: { marginLeft: 20 },
               component: RouterLink,
               color: "primary",
               to: "/",
               variant: "contained",}
             , "ออกจากระบบ"

             )

         , status ? (
           React.createElement('div', null
             , alert ? (
               React.createElement(Alert, { severity: "success",}, "This is a success alert — check it out!"

               )
             ) : (
               React.createElement(Alert, { severity: "warning", style: { marginTop: 20 },}, "This is a warning alert — check it out!"

               )
             )
           )
         ) : null
       )
       , React.createElement('div', { className: classes.root,}
         , React.createElement('form', { noValidate: true, autoComplete: "off",}
         , React.createElement(FormControl, {
             fullWidth: true,
             className: classes.margin,
             variant: "outlined",}
           
             , React.createElement(Autocomplete, {
              id: "docter",
              options: doctor,
              getOptionLabel: (option) => option.doctor_id,
              style: { width: 300 },
              renderInput: (params) => React.createElement(TextField, { ...params, label: "รหัสประจำตัวแพทย์", variant: "outlined",} ),}
    )
           )
         , React.createElement(FormControl, {
             fullWidth: true,
             className: classes.margin,
             variant: "outlined",}
           
             , React.createElement(Autocomplete, {
              id: "patient",
              options: patient,
              getOptionLabel: (option) => option.patient_id,
              style: { width: 300 },
              renderInput: (params) => React.createElement(TextField, { ...params, label: "รหัสผู้เข้ารับการรักษา", variant: "outlined",} ),}
    )
           )



           , React.createElement(FormControl, {
             fullWidth: true,
             className: classes.margin,
             variant: "outlined",}
           
             , React.createElement(Autocomplete, {
              id: "room",
              options: room,
              getOptionLabel: (option) => option.room,
              style: { width: 300 },
              renderInput: (params) => React.createElement(TextField, { ...params, label: "ห้องผ่าตัด", variant: "outlined",} ),}
    )
           )
           , React.createElement('form', { className: classes.margin, noValidate: true,}
              , React.createElement(TextField, {
                id: "datetime-local",
                label: "วัน-เวลาที่ทำการผ่าตัด",
                type: "datetime-local",
                defaultValue: "2017-05-24T10:30",
                className: classes.textField,
                InputLabelProps: {
                shrink: true,
               },}
                 )
            )

           , React.createElement('div', { className: classes.margin,}
             , React.createElement(Button, {
               onClick: () => {
                 CreateUser();
               },
               variant: "contained",
               color: "primary",}
             , "ยืนยันการจองห้อง"

             )
             , React.createElement(Button, {
               style: { marginLeft: 20 },
               component: RouterLink,
               to: "/",
               variant: "contained",}
             , "ย้อนกลับ"

             )
           )
         )
       )
     )
   )
 );
}
